<template>
    <div>
        <div class="title">
            <h3 class="yun-h3"> Checkbox</h3>
            <p> YunUI</p>
        </div>
        <div class="content form">
            <div class="row clearContentPadding">
                <y-form>
                    <div>
                        <y-checkbox :checkboxList="checkList" v-model="checkedList"></y-checkbox>
                    </div>
                </y-form>
                <p>:{{checkedList}}</p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'y-checkbox-demo',
        data () {
            return {
                checkList: [
                    {
                        label: '1',
                        checked: false,
                        value: '1'
                    },
                    {
                        label: '2',
                        checked: false,
                        value: '2'
                    },
                    {
                        label: '3',
                        checked: true,
                        value: '3'
                    }
                ],
                checkedList: []
            }
        },
        created () {
            console.log('checkbox demo loaded')
        },
        methods: {}
    }

</script>

<style lang="less">
</style>



