<template>
    <div>
        <div class="title">
            <h3 class="yun-h3"> Radio</h3>
            <p>YunUI</p>
        </div>
        <div class="content form">
            <div class="row clearContentPadding">
                <y-form>
                    <div>
                        <y-radio :radioList="radioList" v-model="radio"></y-radio>
                        <p>{{radio}}</p>
                    </div>
                </y-form>
            </div>
            <div class="row clearContentPadding">
                <y-form>
                    <div>
                        <y-radio :radioList="radioList2" v-model="radio2"></y-radio>
                        <p>{{radio2}}</p>
                    </div>
                </y-form>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'y-radio-demo',
        data () {
            return {
                radioList: [
                    {
                        label: '1',
                        value: '1',
                        name: 'radio'
                    },
                    {
                        label: '2',
                        value: '2',
                        name: 'radio'
                    },
                    {
                        label: '3',
                        value: '3',
                        name: 'radio'
                    }
                ],
                radioList2: [
                    {
                        label: ' ',
                        checked: true,
                        value: '1',
                        name: 'radio2'
                    },
                    {
                        label: '2',
                        value: '2',
                        name: 'radio2'
                    },
                    {
                        label: '3',
                        value: '3',
                        name: 'radio2'
                    }
                ],
                radio: '',
                radio2: ''
            }
        },
        created () {
            console.log('radio demo loaded')
        },
        methods: {}
    }

</script>

<style lang="less" scoped>
    .row{
        padding:0 0 8px !important;
        margin-bottom:0;
    }
</style>



